import { computed, reactive, toRefs } from 'vue';
import { useTranslate } from '@demo/use-translate';
import { deepClone } from '../../utils/deep-clone';
import zhCNOptions from './area-zh-CN';
import enUSOptions from './area-en-US';
const i18n = {
    'zh-CN': {
        area: '地区',
        options: zhCNOptions,
        selectArea: '请选择地区',
        customColor: '自定义颜色',
        asyncOptions: '异步加载选项',
        asyncOptions1: [
            {
                text: '浙江省',
                value: '330000',
                children: [],
            },
        ],
        asyncOptions2: [
            { text: '杭州市', value: '330100' },
            { text: '宁波市', value: '330200' },
        ],
        customFieldNames: '自定义字段名',
    },
    'en-US': {
        area: 'Area',
        options: enUSOptions,
        selectArea: 'Select Area',
        customColor: 'Custom Color',
        asyncOptions: 'Async Options',
        asyncOptions1: [
            {
                text: 'Zhejiang',
                value: '330000',
                children: [],
            },
        ],
        asyncOptions2: [
            { text: 'Hangzhou', value: '330100' },
            { text: 'Ningbo', value: '330200' },
        ],
        customFieldNames: 'Custom Field Names',
    },
};
export default {
    setup() {
        const t = useTranslate(i18n);
        const state = reactive({
            base: {
                show: false,
                value: '',
                result: '',
            },
            customColor: {
                show: false,
                value: null,
                result: '',
            },
            async: {
                show: false,
                value: null,
                result: '',
                options: t('asyncOptions1'),
            },
            customFieldNames: {
                show: false,
                value: null,
                result: '',
            },
        });
        const fieldNames = {
            text: 'name',
            value: 'code',
            children: 'items',
        };
        const customFieldOptions = computed(() => {
            const options = deepClone(t('options'));
            const adjustFieldName = (item) => {
                if ('text' in item) {
                    item.name = item.text;
                    delete item.text;
                }
                if ('value' in item) {
                    item.code = item.value;
                    delete item.value;
                }
                if ('children' in item) {
                    item.items = item.children;
                    delete item.children;
                    item.items.forEach(adjustFieldName);
                }
            };
            options.forEach(adjustFieldName);
            return options;
        });
        const loadDynamicOptions = ({ value }) => {
            if (value === '330000') {
                setTimeout(() => {
                    state.async.options[0].children = t('asyncOptions2');
                }, 500);
            }
        };
        const onFinish = (type, { value, selectedOptions, }) => {
            const result = selectedOptions
                .map((option) => option.text || option.name)
                .join('/');
            state[type] = {
                ...state[type],
                show: false,
                value,
                result,
            };
        };
        return {
            ...toRefs(state),
            t,
            onFinish,
            fieldNames,
            customFieldOptions,
            loadDynamicOptions,
        };
    },
};
